import React, { Component } from 'react';
import { Card, Input, Button } from 'antd';
// import ChainList from "../../components/ChainSelect"
import '../../App.css';
import { connect } from "react-redux"
import { getLGbalance, getChainInfo } from '../../components/global/functions';
import Logo from '../../logo192.png';

export class top extends Component {
  constructor(props) {
    super(props)
    this.state = {
      user: '',
      oringUser: '',
      LGCbalance: '',
      Color: '#001e78',
    }
  }

  async componentWillReceiveProps(nextProps) {
    if (nextProps.UserAdr !== this.props.UserAdr
      || nextProps.ChainID !== this.props.ChainID) {
      await this.opUser()
      this.chainId(window.ChainID)
    }
  }

  async componentDidMount() {
    await this.chainId(window.ChainID)
    await this.opUser()
  }

  chainId = async (chainId) => {
    this.props.sendStore("ChainID", chainId)
    await getChainInfo(chainId).then((result) => {
      this.props.sendStore("ChainName", result.ChainName)
    })
  }

  opUser = async () => {
    if (window.defaultAccount === 'undefined') { return }
    await this.props.sendStore("UserAdr", window.defaultAccount)
    const _user = this.props.UserAdr;
    let userLength
    if (typeof _user == 'string') { userLength = _user.length } else {
      return
    }
    let user = _user.substring(0, 6) + '……' + _user.substring(userLength - 4);
    this.setState({
      user,
      oringUser: _user
    });
    this.props.sendStore("LGbalance", '0')
    await getLGbalance().then((result) => {
      this.props.sendStore("LGbalance", result)
    })
    this.COLOR()
  }

  changUser = () => {
    this.setState({ user: this.state.oringUser })
  }

  COLOR = () => {
    let Color = '#001e78'
    if (Number(this.state.LGCbalance) >= 10 && Number(this.props.LGbalance) > 1000) {
      Color = '#5fac40'
    }
    if (Number(this.props.LGbalance) > 10000) {
      Color = '#e8c036'
    }
    this.setState({ Color })
  }

  render() {

    return (
      <div>
        <Card type="inner" size='small' style={{ borderRadius: '21px', background: this.state.Color }}
          bodyStyle={{ padding: '5px 6px 5px 6px' }}>
          <div>
            <Button style={{ float: 'left', width: '27%', borderWidth: '0px', borderRadius: '16px 0 0 16px' }}>
              {this.props.ChainName}
            </Button>
            <div onClick={() => { this.changUser() }} style={{ float: 'left', width: '46%' }}>
              <Input id='i1' value={this.state.user} disabled style={{ borderRadius: '0' }} />
            </div>
            <Button style={{
              float: 'right', width: '27%', borderWidth: '0px', borderRadius: '0 16px 16px 0 ',
              paddingLeft: '3px'
            }}
              onClick={() => { this.opUser() }}
            ><img style={{ maxWidth: 'auto', height: '23px' }} src={Logo} alt="" />
              {this.props.LGbalance}
            </Button>
          </div>
        </Card>
      </div>
    )
  }
}


const mapStateToProps = (state) => {
  return {
    ChainID: state.ChainID,
    UserAdr: state.UserAdr,
    LGbalance: state.LGbalance,
    ChainName: state.ChainName
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    sendStore(tp, val) {
      const action = { type: tp, value: val }
      dispatch(action)
    }
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(top)